import React from 'react';

const style = {
    container: {
        width: '100%',
        display: 'flex',
        padding: '0 20px',
    },

    slide: {
    height: '80vh',
    borderRadius: '20px',
    margin: '10px',
    cursor: 'pointer',
    color: 'white',
    flex: '1',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    position: 'relative',
    transition: 'all 500ms ease-in-out'
},

// .slide h3 {
//     position: absolute;
//     font-size: 24px;
//     bottom: 20px;
//     left: 20px;
//     margin: 0;
//     opacity: 0;
// }
//
// .slide.active {
//     flex: 10;
// }
//
// .slide.active h3 {
//     opacity: 1;
//     transition: opacity 0.3s ease-in 0.4s;
// }
}

const Gallery = (props) => {
    return (
        <div className='container'>
            {props.galleryImg.map(img => (
                <div className='slide'
                     style={{'background-image': `url(${img.url})`}}
                >
                    <h3>{img.name}</h3>
                </div>
                )
            )}
        </div>
    );
};

export default Gallery;


